<template>
  <div class="bargainBox boxBorder" :class="active" @mouseover="mouseOver" @mouseleave="mouseLeave">
    <div class="listType" :class="`${'listType'+itemData.listType}`">
      <!-- 砍价标题 -->
      <div class="bargainTitle flex-row-start">
        <div class="titleImg">
          <img class="kjTips" src="@/assets/decorateBox/goods/kj.png" alt />
        </div>
        <div>{{itemData.title}}</div>
      </div>
      <ul class="goods">
        <li class="item" v-for="(item,index) in itemData.goodsData" :key="index">
          <div class="warningBox" v-if="item.activityStatus == 0">活动未开始，商品仅预览。活动开始后展示此商品</div>
          <div class="warningBox" v-if="item.activityStatus == 2 || itemData.activityStatus == 3">活动已结束，此商品不作展示，请尽快删除~</div>
          <div class="flex-row-start">
            <div class="imgBox">
              <img v-if="item.imageUrl" :src="item.imageUrl" alt="">
              <div v-else class="blueBox"></div>
              <img v-if="item.activityStatus != 1" class="noShow" src="../../../../../assets/images/activity/noshow.png" alt="">
            </div>
            <div class="infoBox">
              <div class="name">{{item.productName}}</div>
              <div class="priceBox flex-row-between">
                <div class="flex-row-start">
                  <div class="flex-row-start red">
                    <div class="small">底价:</div>
                    <div class="big">￥{{item.activityPrice}}</div>
                  </div>
                  <div class="linePrice" v-show="itemData.lineation">￥{{item.minPrice}}</div>
                </div>
                <div class="btn">去砍价</div>
              </div>
            </div>
          </div>
        </li>
      </ul>
      <div class="moreBox flex-row-center" v-show="itemData.showMore">
        <p>查看更多</p>
        <img class="more" src="@/assets/decorateBox/shop_info_arrow.png" alt />
      </div>
    </div>
    <!-- 左侧介绍 -->
    <span class="box-title">{{itemData.name}}</span>
    <i class="delete-btn" v-if="deleteBtn" @click.stop="deleteBox(itemData)">
      <img src="@/assets/decorateBox/deleteBox.png" alt />
    </i>
  </div>
</template>

<script>
import { getBargainSubgroupInfo } from '@/api/marketManage/bargain'
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      active: "", //绑定样式
      deleteBtn: false
    };
  },
  created(){
    console.log('砍价组件数据',this.itemData)
    if(this.itemData.bargainIds.length > 0) this.getData()
  },
  methods: {
    getData(){
      getBargainSubgroupInfo(this.itemData.bargainIds.join(',')).then(res=>{
        console.log('砍价组件回显数据',res)
        if(res.code == 200){
          let goodsData = res.data;
          let bargainIds = []
          goodsData.forEach(e => bargainIds.push(e.bargainId))
          this.itemData.goodsData = goodsData
          this.itemData.bargainIds = bargainIds
        }
      })
    },
    // 移入事件
    mouseOver() {
      this.active = "active";
      this.deleteBtn = true;
    },
    // 移出事件
    mouseLeave() {
      this.active = "";
      this.deleteBtn = false;
    },
    //删除当前组件盒子事件
    deleteBox(box) {
      this.$emit("deleteBox", box);
    }
  }
};
</script>

<style scoped lang="less">
img{
  width: 100%;
  height: 100%;
  vertical-align: top;
}
.delete-btn {
  position: absolute;
  width: 32px;
  height: 32px;
  cursor: pointer;
  z-index: 3;
  top: 10px;
  right: -32px;
}
.box-title {
  position: absolute;
  cursor: pointer;
  z-index: 3;
  top: 0px;
  left: -70px;
  height: 24px;
  font-size: 12px;
  background: rgba(255, 255, 255, 1);
  box-shadow: 2px 2px 4px 0px rgba(80, 80, 80, 0.2);
  color: rgba(102, 102, 102, 1);
  line-height: 24px;
  width: 64px;
  text-align: center;
  border-radius: 2px;
}
.box-title:after,
.box-title:before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border: 4px solid transparent;
  left: 50%;
  margin-left: 32px;
  bottom: 8px;
}
.oldPriceShow {
  opacity: 0;
}
.box-title:after {
  border-left-color: #fff;
}
.listType0{
  border-radius: 6px;
}
.listType1{
  border-radius: 6px;
  box-shadow: rgba(153, 153, 153, 0.5) 0px 2px 2px 0px;
}
.listType3{
  box-shadow: rgba(153, 153, 153, 0.5) 0px 2px 2px 0px;
}
.blueBox{
  width: 100%;
  height: 100%;
  background-color: #E5F1FE;
}
.bargainBox{
  width: 100%;
  padding: 12px 10px;
  .listType{
    background-color: #fff;
    padding: 10px;
    // border: 1px solid red;
  }
  .bargainTitle{
    height:21px;
    line-height: 21px;
    color: #FF3C3C;
    font-size: 16px;
    font-family: PingFangSC;
    font-weight: 500;
    letter-spacing: 0px;
    .titleImg{
      width: 16px;
      height: 16px;
      margin-right: 3px;
    }
  }
  .goods{
    width: 100%;
    padding: 12px 0;
    .item{
      position: relative;
      margin-top: 8px;
      width: 100%;
      &:first-child{
        margin-top: 0;
      }
      .warningBox{
        z-index: 20;
        position: absolute;
        left: 0;
        bottom: 0;
        text-align: center;
        width: 100%;
        background-color: rgba(80, 80, 80, 0.6);
        font-size: 12px;
        color: #fff;
        line-height: 32px;
        height: 32px;
        font-weight: 400;
      }
      .imgBox{
        width: 110px;
        height: 110px;
        margin-right: 10px;
        position: relative;
        .noShow{
          width: 52px;
          height: 52px;
          position: absolute;
          left: 0;
          right: 0;
          top: 16px;
          margin:0 auto;
          z-index: 10;
        }
      }
      .infoBox{
        box-sizing: border-box;
        width: 215px;
        height: 110px;
        padding-top: 15px;
        .name{
          height: 40px;
          opacity: 1;
          font-size: 14px;
          font-family: PingFangSC;
          font-weight: 400;
          color: #393939;
          line-height: 20px;
          letter-spacing: 0px;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          line-clamp: 2;
          -webkit-box-orient: vertical;
        }
        .priceBox{
          margin-top: 15px;
          height: 26px;
          line-height: 26px;
          .red{
            color: #FF4444;
            font-weight: 500;
            .small{
              font-size: 12px;
            }
            .big{
              font-size: 16px;
            }
          }
          .linePrice{
            color: #D4D4D4;
            font-size: 12px;
            text-decoration: line-through;
            font-weight: 500;
          }
          .btn{
            padding: 0 8px;
            background-color:#FF4444;
            color: #fff;
            text-align: center;
            line-height: 26px;
            height: 26px;
            font-size: 14px;
            font-family: PingFangSC;
            font-weight: 500;
            letter-spacing: 0px;
            border-radius: 13px;
          }
        }
      }
    }
    
  }
  .moreBox{
    height: 20px;
    line-height: 20px;
    font-size: 12px;
    font-family: PingFangSC;
    font-weight: 400;
    color: #999;
    letter-spacing: 0px;
    .more{
      margin-left: 4px;
      width: 4px;
      height: 8px;
    }
  }
}

</style>
